import {dom, hyphenate} from '@o2oa/util';
import {$Input} from './$input.js';
import css from './template/select.scope.css?raw';

export default class OOSelect extends $Input{
    static prop = {
        read: false
    };
    static get observedAttributes() {
        OOSelect.prop = Object.assign({}, $Input.prop, OOSelect.prop);
        return Object.keys(OOSelect.prop).map((att)=>{
            return hyphenate(att);
        });
    }
    constructor() {
        super();
        this.#setPropMap();
        this._init('oo-select');
        this._useCss(css);
    }
    #setPropMap(){
        this._setPropMap = Object.assign(this._setPropMap, {
            disabled: ()=>{
                dom.toggleAttr(this._elements.input, 'disabled', this._props.disabled);
                dom.checkClass(this._elements.box, 'disabled', this._props.disabled);
                dom.checkClass(this._elements.label, 'disabled', this._props.disabled);
                dom.checkClass(this._elements.drop, 'disabled', this._props.disabled);
            },
            value: ()=>{
                if (this._props.value){
                    for (const option of this.#options){
                        if (option.value===this._props.value){
                            option.selected = true;
                            break;
                        }
                    }
                }
            },
            readonly:()=>{},
            readmode: ()=>{
                // dom.toggleAttr(this._elements.input, 'readonly', this._props.readmode);
                dom.checkClass(this._elements.box, 'readmode', this._props.readmode);
                dom.checkClass(this._elements.label, 'readmode', this._props.readmode);
            },
            autoSize: ()=>{
                dom.setStyle(this._elements.view, 'width', `${this.#maxWidth}px`);
            },
            read: ()=>{}
        });
    }
    #droped = false;
    #options = [];
    #optionsArea = null;
    #hideOptionsFun = null;
    #groups = [];
    _render(){
        this.#droped = false;
        this._elements.drop = dom('div.drop.ooicon-drop_down');
        this._elements.view.insertAdjacentElement('afterend', this._elements.drop);

        this.#optionsArea = this.querySelector('div.options-area');
        if (!this.#optionsArea){
            this.#optionsArea = dom('div.options-area.invisible');
            dom.set(this.#optionsArea, 'html', '<div class="arrow"></div><div class="options-content"><slot name="items"></slot></div>')
            // this._content.insertAdjacentElement('beforeend', this.#optionsArea);
            this._elements.box.insertAdjacentElement('beforeend', this.#optionsArea);
        }

        this._elements.input.setAttribute('readonly', true);
    }
    _removeItem(option){
        this.#options.splice(this.#options.indexOf(option), 1);
        option.removeAttribute('slot');
    }
    #maxWidth = 0;
    _appendItem(option){
        this.#options.push(option);
        option.setAttribute('slot', 'items');
        option.setAttribute('skin', this._props.skin);

        const viewSize = dom.getSize(this._elements.view);
        const sizeOption = dom.getSize(option._elements.area);

        const size = Math.max(sizeOption.x, viewSize.x);
        if (size>this.#maxWidth){
            this.#maxWidth = size;
            this.#setBoxMinWidth();
        }

        if (this._props.size) option.setAttribute('size', this._props.size);

        this._appendItemCheckValue(option);

        this._setPropMap.$default('value');
    }
    checkMaxWidth(width){
        if (width>this.#maxWidth){
            this.#maxWidth = width;
            this.#setBoxMinWidth();
        }
    }
    #setBoxMinWidth(){
        if (this._props.autoSize){
            if (this.setMinWidthTimer) window.clearTimeout(this.setMinWidthTimer);
            this.setMinWidthTimer = setTimeout(()=>{
                dom.setStyle(this._elements.view, 'width', `${this.#maxWidth}px`);
                this.setMinWidthTimer = null;
            }, 10);
        }
    }

    _appendItemCheckValue(option){
        if (this._props.value){
            if (option.value===this._props.value){
                option.selected = true;
            }
        }
    }
    _removeGroup(group){
        this.#groups.splice(this.#groups.indexOf(group), 1);
        group.removeAttribute('slot');
    }
    _appendGroup(group){
        this.#groups.push(group);
        group.setAttribute('slot', 'items');
        group.setAttribute('skin', this._props.skin);
        if (this._props.size) option.setAttribute('size', this._props.size);
    }
    #selectedOption = null;
    _selected(option){
        if (option.selected){
            if (this.#selectedOption && this.#selectedOption !== option){
                this.#selectedOption.selected = false;
            }

            this.#selectedOption = option;
            const text = option._props.text || option._elements.label.textContent;
            // this._elements.input.value = text;
            this._elements.viewText.textContent = text;
            this.value = option._props.value || text;
            this.text = text;

            this.unInvalidStyle();

            this.dispatchEvent(new CustomEvent('change'));
        }
        this.#hideOptions();
    }
    #toggleOptions(){
        this.#droped = !this.#droped;
        if (this.#droped){
            this.#showOptions()
        }else{
            this.#hideOptions()
        }
    }
    _setEvent() {
        super._setEvent();
        this._elements.box.addEventListener('click', ()=>{
            if (!this._props.disabled &&  !this._props.readmode && !this._props.read) this.#toggleOptions();
        });
        this._elements.input.addEventListener('click', (e)=>{
            e.stopPropagation();
        });
        // this._elements.viewText.addEventListener('click', ()=>{
        //     this.#toggleOptions()
        // });

        this.#optionsArea.addEventListener('click', (e)=>{
            e.stopPropagation();
        });
        this.#optionsArea.addEventListener('mousedown', (e)=>{
            e.stopPropagation();
        });
        this._content.firstElementChild.addEventListener('mousedown', (e)=>{
            e.stopPropagation();
        });

        // if (this._elements.drop){
        //     this._elements.box.addEventListener('mouseover', (e)=>{
        //         dom.removeClass(this._elements.drop, 'ooicon-drop_down');
        //         dom.addClass(this._elements.drop, 'ooicon-close');
        //     })
        // }
    }
    #positionOptions(){
        const position = dom.getPosition(this._elements.box, this._content);
        const size = dom.getSize(this._elements.box);
        const x = position.x;
        const y = position.y + size.y + 6 + size.y*0.2;
        const w = size.x;

        dom.setStyles(this.#optionsArea, {
            top: y+'px',
            // left: x+'px',
            // width: w+'px',
            width: '100%'
        });
        const arrowLeft = w/2-6;
        dom.setStyles( this.#optionsArea.querySelector('.arrow'), {
            left: arrowLeft+'px'
        });
    }
    #toggleStyles(){
        dom.checkClass(this._elements.drop, 'down', this.#droped);
        dom.checkClass(this._elements.box, 'focus', this.#droped);
        dom.checkClass(this._elements.label, 'focus', this.#droped);
    }
   #showOptions(){
        if (!this._props.readmode){
            this.#droped = true;
            this.#toggleStyles();
            this.#positionOptions();
            dom.removeClass(this.#optionsArea, 'invisible');
            dom.addClass(this.#optionsArea, 'visible');

            this.ownerDocument.dispatchEvent(new MouseEvent("mousedown"));

            this.#hideOptionsFun =  this.#hideOptions.bind(this);
            this.ownerDocument.addEventListener('mousedown', this.#hideOptionsFun);

            this.unInvalidStyle();
        }
    }
    #hideOptions(){
        this.#droped = false;
        this.#toggleStyles();
        dom.removeClass(this.#optionsArea, 'visible');
        dom.addClass(this.#optionsArea, 'invisible');
        this.ownerDocument.removeEventListener('mousedown', this.#hideOptionsFun);
    }

    // checkValidity(){
    //     this._elements.input.removeAttribute('readonly');
    //
    //     this.dispatchEvent(new CustomEvent("validity"));
    //     const flag = this._elements.input.checkValidity(...arguments);
    //
    //     this._elements.input.setAttribute('readonly', true);
    //
    //     return flag;
    // }
    // reportValidity() {
    //     this._elements.input.removeAttribute('readonly');
    //
    //     this.dispatchEvent(new CustomEvent("validity"));
    //     const flag = this._elements.input.reportValidity(...arguments);
    //
    //     this._elements.input.setAttribute('readonly', true);
    //
    //     return flag;
    // }
    get value(){
        return this._props.value;
    }
    set value(v){
        this.setAttribute('value', v);
    }
    get text(){
        return this._props.text;
    }
    set text(v){
        this._props.text = v;
    }
}
