import {dom, hyphenate} from '@o2oa/util';
import {OOComponent} from '../base.js';
import css from './template/option.scope.css?raw';

const html = '<div class="content"><div class="area"><div class="text"></div><div class="label"></div></div></div>';
export default class OOOption extends OOComponent{
    static prop = {
        value: '',
        text: '',
        selected: false,
        disabled: false,
        skin: ''
    };
    static get observedAttributes() {
        return Object.keys(this.prop).map((att)=>{
            return hyphenate(att);
        });
    }
    _setPropMap={
        value: ()=>{},
        $default: ()=>{},
        text: ()=>{
            this.#renderText();
        },
        disabled: ()=>{
            dom.checkClass(this._content, 'disabled', !!this._props.disabled);

        },
        selected: ()=>{
            dom.checkClass(this._content, 'selected', this._props.selected);
            this.#select._selected(this);
        },
        skin: ()=>{
            this._useSkin(this._props.skin);
        },
    };
    _elements={
        text: null,
        label: null,
        area: null
    }
    constructor() {
        super();
        this._initialize('oo-option', OOOption, html, css);
    }
    _render(){
        this.#renderText();
        this._setPropMap.disabled();
    }
    #renderText(){
        if (this.innerHTML.trim()){
            let node = this.firstChild;
            while(node){
                this._elements.label.appendChild(node);
                node = this.firstChild;
            }
        }
        if (!this._elements.label.innerHTML.trim()) {
            if (this._props.text){
                this._elements.text.textContent = this._props.text;
            }else{
                this._elements.text.innerHTML = '&nbsp;';
            }
        }
    }
    #select = null;
    #group = null;
    _connected() {
        const parent = this.parentElement;
        if (parent.tagName.toLowerCase()==='oo-option-group') {
            this.#group = parent;
            if ( parent.parentElement.tagName.toLowerCase()==='oo-select') {
                this.#select = parent.parentElement;
            }
        }else if (parent.tagName.toLowerCase()==='oo-select') {
            this.#select = parent;
        }
        if (this.#group || this.#select){
            (this.#group || this.#select )._appendItem(this);
            dom.show(this);
        }else{
            dom.hide(this);
        }
    }
    _disconnected(){
        if (this.#group || this.#select){
            const parent = this.#group || this.#select;
            this.#select = null;
            this.#group = null;
            parent._removeItem(this);
        }
    }

    _setEvent(){
        this._content.addEventListener('click', ()=>{
            if (!this._props.disabled) this.selected = true;
        });

        const checkMaxWidth = ()=>{
            const sizeOption = dom.getSize(this._elements.area);
            this.#select?.checkMaxWidth(sizeOption.x);
        }
        const observer = new MutationObserver(checkMaxWidth);
        observer.observe(this._content, {
            subtree: true,
            childList: true,
            attributes: true,
            characterData: true
        });
    }

    // get value(){
    //     return this._props.value;
    // }
    // set value(v){
    //     this._props.value = v;
    // }
    // get selected(){
    //     return this._props.selected;
    // }
    // set selected(v){
    //     this._props.selected = v;
    //     this._setPropMap.selected();
    // }

}
