import {dom, hyphenate} from '@o2oa/util';
import {OOComponent} from '../base.js';
import html from './template/$check.html?raw';
import css from './template/$check.scope.css?raw';

class $Check extends OOComponent{
    static prop = {
        checked: false,
        disabled: false,
        text: '',
        value: '',
        name: '',
        size: '',
        skin: '',
        readmode: false
    };
    static get observedAttributes() {
        return Object.keys(this.prop).map((att)=>{
            return hyphenate(att);
        });
    }
    _elements={
        label: null,
        button: null,
        text: null,
        slot: null,
        check: null,
        input: null,
        checkIcon: null
    };
    _setPropMap={
        text: ()=>{
            this._elements.text.textContent = this._props.text;
        },
        value: ()=>{
            this._elements.input.value = this._props.value;
            this._elements.input.setAttribute('value', this._props.value);
        },
        name: (old)=>{
            if (old !== this._props.name){
                this._elements.input.name = this._props.name;
                this._elements.input.setAttribute('name', this._props.name);
                this.setAttribute('name', this._props.name);
            }
        },
        checked: ()=>{
            this._elements.input.checked = !!this._props.checked;
            this._render();
        },
        disabled: ()=>{
            this._elements.input.disabled = !!this._props.disabled;
            this._render();
        },
        size: ()=>{
            if (this._props.size){
                dom.setStyle(this._elements.button, 'font-size', this._props.size);
            }else{
                dom.setStyle(this._elements.button, 'font-size', 'auto');
            }
        },
        skin: ()=>{
            this._useSkin(this._props.skin);
        },
        readmode: ()=>{
            if (this._props.readmode){
                if (!this.checked){
                    dom.addClass(this, 'hide');
                }
                dom.addClass(this._elements.button, 'hide');
                dom.addClass(this._elements.label, 'readmode');
                this._elements.input.disabled = true
            }else{
                dom.removeClass(this, 'hide');
                dom.removeClass(this._elements.button, 'hide');
                dom.removeClass(this._elements.label, 'readmode');
                if (!this._props.disabled) this._elements.input.disabled = false;
            }
        },
        $default: ()=>{}
    };
    constructor() {
        super();
    }
    _init(name, clazz, ohtml, ocss){
        this._initialize(name,clazz||$Check, ohtml||html, ocss||css);
    }
    _setEvent(){
        super._setEvent();
        // //避免两次click事件触发
        // this.shadowRoot.addEventListener('click', (event) => {
        //     if (event.target.tagName!=='INPUT'){
        //         event.stopPropagation();
        //     }
        // });

        this._elements.input.addEventListener('change', (e)=>{
            this.checked = this._elements.input.checked;
            this.dispatchEvent(new Event("change", e));
        });
    }
    _unchecked(){
        this._elements.input.checked = false;
        this._props.checked = false;
        this._render();
    }
    _render(){
        let node = this.firstChild;
        while(node){
            this._elements.slot.appendChild(node);
            node = this.firstChild;
        }

        const checked = this._elements.input.checked;
        dom.checkClass(this._elements.button, 'checked', checked);
        dom.checkClass(this._elements.check, 'checked', checked);
        dom.checkClass(this._elements.label, 'checked', checked);
        dom.checkClass(this._content, 'checked', checked);
        dom.checkClass(this, 'checked', checked);
        // dom.toggleAttr(this, 'checked', checked.toString());

        const disabled = this._elements.input.disabled;
        dom.checkClass(this._elements.button, 'disabled', disabled);
        dom.checkClass(this._elements.check, 'disabled', disabled);
        dom.checkClass(this._elements.label, 'disabled', disabled);

        this._setPropMap.text();
    }

    group = null;
    _connected() {
        this._render();
        const name = this.tagName.toLowerCase()+'-group';
        const group = dom.getParent(this, name);
        if (group){
            this.group = group;
            setTimeout(()=>{
                group._appendItem?.(this);
            }, 10);
        }
    }
    _disconnected(){
        if (this.group){
            const group = this.group;
            this.group = null;
            group._removeItem(this);
        }
    }

    get checked(){
        return this._elements.input.checked;
    }
    set checked(value){
        const dispatchEventFlag = this.checked!==value;
        this._elements.input.checked = !!value;
        this._render();
        if (dispatchEventFlag){
            this.dispatchEvent(new Event("change"));
        }
    }

    // checkValidity(){
    //     this.dispatchEvent(new CustomEvent("validity"));
    //     return this._elements.input.checkValidity(...arguments);
    // }
    // reportValidity() {
    //     this.dispatchEvent(new CustomEvent("validity"));
    //     return this._elements.input.reportValidity(...arguments);
    // }
    //
    // #checkInvalid(detail){
    //     this.dispatchEvent(new CustomEvent("invalid", {detail}));
    //     dom.addClass(this._elements.input, 'invalid');
    //     const invalidMessage = this.#setValidationMessage();
    //     if (invalidMessage){
    //         if (this.group){
    //             debugger;
    //             this.group.checkInvalid(detail, invalidMessage);
    //         }
    //     }
    //         // dom.addClass(this._elements.invalidHint, 'show');
    // }
    //
    // #setValidationMessage(){
    //     if (!this._elements.input.validity.customError){
    //         if (this._props.validity) this._elements.input.setCustomValidity(this._props.validity);
    //     }
    //     if (this._elements.invalidHint) this._elements.invalidHint.textContent = this._elements.input.validationMessage;
    //     return this._elements.input.validationMessage;
    // }

    // get value(){
    //     return this._elements.input.value;
    // }
    // set value(v){
    //     this._props.value = v;
    //     this._setPropMap.value();
    // }

    // get text(){
    //     return this._props.text;
    // }
    // set text(v){
    //     this._props.text = v;
    //     this._setPropMap.text();
    // }
    // get name(){
    //     return this._props.name;
    // }
    // set name(v){
    //     this._props.name = v;
    //     this._setPropMap.name();
    // }
}
export {$Check}
