import {dom, hyphenate} from '@o2oa/util';
import {OOComponent} from '../base.js';
import css from './template/optionGroup.scope.css?raw';

const html = `
<div class="content">
    <div class="text"></div>
    <div class="label"></div>
    <div class="options-content">
        <slot name="items"></slot>
    </div>
</div>`;
export default class OOOptionGroup extends OOComponent{
    static prop = {
        text: '',
        skin: ''
    };
    static get observedAttributes() {
        return Object.keys(this.prop).map((att)=>{
            return hyphenate(att);
        });
    }
    _setPropMap={
        $default: ()=>{},
        text: ()=>{
            this._elements.text.textContent = this._props.text;
        },
        label: ()=>{
            this._elements.label.textContent = this._props.label;
        },
        skin: ()=>{
            this._useSkin(this._props.skin);
        },
    };
    _elements={
        text: null,
        label: null
    }
    _render() {
        this._elements.text.textContent = this._props.text || this._props.label;
    }
    constructor() {
        super();
        this._initialize('oo-option-group', OOOptionGroup, html, css);
    }
    #select = null;
    #options = [];
    _connected() {
        const select = this.parentElement;
        if (select.tagName.toLowerCase()==='oo-select') {
            this.#select = select;
        }
        if (this.#select){
            this.#select._appendGroup(this);
            dom.show(this);
        }else{
            dom.hide(this);
        }

    }
    _disconnected(){
        if (this.#select){
            const group = this.#select;
            this.#select = null;
            group._removeGroup(this);
        }
    }
    _removeItem(option){
        if( this.#select )this.#select._removeItem(option)
        this.#options.splice(this.#options.indexOf(option), 1);
    }
    _appendItem(option) {
        if( this.#select )this.#select._appendItem(option)
        this.#options.push(option);
    }
}
