 import {dom, typeOf, htmlFilter, hyphenate} from "@o2oa/util";
import '../web-component/feedback/dialog.js';

class Dialog{
    options = {
        css: '',
        cssLink: '',
        skin: '',
        isShow: false,
        title: '',
        canResize: false,
        canMove: true,
        canClose: true,
        icon: '',
        buttons: 'ok, cancel',
        // ok: 'OK',
        // yes: 'Yes',
        // no: 'No',
        // cancel: 'Cancel',
        content: null,
        position: 'center center',  //center, left top, right top, left bottom, right bottom, none
        offset: {
            x: 0,
            y: 0
        },
        zIndex: 100,
        modal: true,
        modalArea: null,
        width: '',
        height: '',
        maxWidth: '',
        events: {},
        buttonAlign: 'flex-end',
        positionNode: null,
        okSize: ''
    };
    constructor(container, options) {
        const containerNode = Event.prototype.isPrototypeOf(container) ? dom.getPositionParent(container.currentTarget) : container
        this.options = Object.assign(this.options, options);
        this.container = containerNode || document.body;
        this.positionNode = this.options.positionNode || this.container;
        this.area = this.options.area || this.container;

        if (this.options.modal && !this.options.modalArea){
            this.options.modalArea = this.container;
        }
    }
    async show(position) {
        if (position) this.options.position = position;
        if (!this.dialog) this._createDialog();
        await this._positionDialog();
        if (this.options?.attr) dom.set(this.dialog, this.options?.attr);
        this.dialog.show();
        if (this.options.modal && this.options.modalArea) this._showModal();
    }
    hide(){
        this.dialog.hide();
    }
    close(){
        this.dialog.close();
        this.dialog = null;
    }
    #removeModal(){
        if (this.modalNode){
            this.modalNode.addEventListener('transitionend', (e)=>{
                this.modalNode.remove();
                this.modalNode = null;
            });
            dom.setStyles(this.modalNode, {opacity: 0});
        }
    }
    _showModal(){
        const zIndex = dom.getStyle(this.dialog, 'z-index');
        this.modalNode = dom('div.oo-dialog-modal');
        this.dialog.insertAdjacentElement('beforebegin', this.modalNode);
        // this.options.modalArea.prepend(this.modalNode);

        const offsetNode = dom.getOffsetParent(this.modalNode);
        if (offsetNode !== this.options.modalArea){
            const p = dom.getPosition(this.options.modalArea, offsetNode);
            const left = p.x+'px';
            const top = p.y+'px';
            dom.setStyles(this.modalNode, {left, top});
        }
        dom.setStyles(this.modalNode, {opacity: 0.3, 'z-index': zIndex-1});
    }

    dialogEvents = [];
    _createDialog(){
        this.dialog = dom('oo-dialog', {styles: {position: 'absolute'}});
        ['css', 'cssLink', 'skin', 'isShow', 'okSize', 'title', 'canResize', 'canMove', 'canClose', 'icon', 'buttons', 'ok', 'yes', 'no', 'cancel', 'buttonAlign', 'closeOnEvent'].forEach((k)=>{
            if (this.options.hasOwnProperty(k)) this.dialog.setAttribute(hyphenate(k), this.options[k]);
        });

        if (this.options.events){
            Object.keys(this.options.events).forEach((k)=>{
                this.dialog.addEventListener(k, this.options.events[k]);
            });
        }

        if (this.dialogEvents && this.dialogEvents.length){
            this.dialogEvents.forEach(({type, listener})=>{
                this.dialog.addEventListener(type, listener);
            });
        }

        const zIndex = this.options.zIndex || 100;
        dom.setStyle(this.dialog,'z-index', zIndex);
        this.#setContent(this.options.content);
        this.container.insertAdjacentElement('beforeend', this.dialog);

        // this.container.prepend(this.dialog)

        if (this.options.width) dom.setStyle(this.dialog,'width', this.options.width);
        if (this.options.height) dom.setStyle(this.dialog,'height', this.options.height);
        if (this.options.maxWidth) dom.setStyle(this.dialog,'max-width', this.options.maxWidth);

        this.dialog.addEventListener('hide', ()=>{
            this.#removeModal();
        })
    }
    #setContent(content){
        if (content){
            const type = typeOf(content);
            switch (type){
                case 'element':
                    this.dialog.append(content);
                    break;
                case 'string':
                    this.dialog.append(dom('div', {html: htmlFilter(content)}));
                    break;
                case 'promise':
                    content.then((v)=>{
                        this.#setContent(v);
                    }).catch(()=>{});
                    break;
            }
        }
    }

    #getPositionN(n, p, dialogSize, offset){
        const defaultName = n==='y' ? 'top' : 'left';
        const max = `calc(100% - ${dialogSize[n]}px)`;
        const center = p[n]==='center' ? `50% - ${dialogSize[n] / 2}px` : '0px';
        const styleName = p[n]==='center' ? defaultName : p[n];
        const style = {};
        style[styleName] = `clamp(0%, calc(${center} + ${(this.options.offset[n]||0)+'em'} + ${offset[n]}px), ${max})`;
        return style;
    }
    #getPosition(p, dialogSize){
        const positionNode = (this.options.positionNode && Event.prototype.isPrototypeOf(this.options.positionNode)) ? this.options.positionNode.target : this.options.positionNode;
        const offset = {x: 0, y: 0};

        if (positionNode){
            const {x, y} = dom.getPosition(positionNode, this.container);
            const positionSize = dom.getSize(positionNode);
            const containerSize = dom.getSize(this.container);

            switch (p.x){
                case 'center':
                    offset.x = x-containerSize.x/2+positionSize.x/2;
                    break;
                case 'left':
                    offset.x = x;
                    break;
                case 'right':
                    offset.x = containerSize.x - x - positionSize.x;
                    break;
            }
            switch (p.y){
                case 'center':
                    offset.y = y-containerSize.y/2+positionSize.y/2;
                    break;
                case 'top':
                    offset.y = y;
                    break;
                case 'bottom':
                    offset.y = containerSize.y - y - positionSize.y;
                    break;
            }
        }
        return {
            ...this.#getPositionN('x', p, dialogSize, offset),
            ...this.#getPositionN('y', p, dialogSize, offset)
        }
    }
    async _getDialogPosition() {
        const dialogSize = await new Promise((resolve) => {
            window.setTimeout(() => {
                resolve(dom.getSize(this.dialog));
            });
        });
        if (dialogSize.y<300){
            this.dialog.setAttribute('size', 'small');
        }
        const origin = this.options.position || 'center center';
        const p = origin.split(/\s+/);
        const style = this.#getPosition({
            x: p[0],
            y: p.length>1 ? p[1] : p[0]
        }, dialogSize);
        style['transform-origin'] = origin;
        return style;
    }
    async _positionDialog() {
        if (this.options.position && this.options.position!=='none'){
            const style = await this._getDialogPosition();
            dom.setStyles(this.dialog, style)
        }
    }
    addEventListener(type, listener){
        if (this.dialog){
            this.dialog.addEventListener(type, listener);
        }else{
            this.dialogEvents.push({type, listener});
        }
    }
}

function dialog(title, content, container, options={}){
    const containerNode = container || document.body;
    const opt = {title, content};

    return new Promise((resolve)=>{
        const buttons = options.buttons || 'ok, cancel';
        const dlg = new Dialog(containerNode, Object.assign(opt, options, {closeOnEvent: false}));

        buttons.split(/,\s*/g).forEach((status)=>{
            dlg.addEventListener('resolve'+status, (e)=>{
                resolve({dlg, status});
            })
        });

        dlg.show();
    });
}

const confirm = {
    async msg(title, content, container, positionNode, position = 'right top', options = {}, type = 'info') {
        const containerNode = container || document.body;
        if (!positionNode) {
            position = 'center center';
        }
        const styleMap = {
            info: {
                icon: 'info',
                skin: ''
            },
            error: {
                icon: 'error',
                skin: 'icon-color: var(--oo-color-error)'
            },
            warn: {
                icon: 'help',
                skin: 'icon-color: var(--oo-color-warn)'
            },
            success: {
                icon: 'check',
                skin: 'icon-color: var(--oo-color-success)'
            }
        };
        return await dialog(title, content, containerNode, Object.assign({canClose: false, maxWidth: '40vw'}, options, {positionNode, position}, styleMap[type]));
    },
    async info(title, content, container, positionNode, position = 'right top', options={}){
        return await this.msg(...arguments);
    },
    async warn(title, content, container, positionNode, position = 'right top', options={}){
        return await this.msg(title, content, container, positionNode, position, options, 'warn');
    },
    async error(title, content, container, positionNode, position = 'right top', options={}){
        return await this.msg(title, content, container, positionNode, position, options, 'error');
    },
    async success(title, content, container, positionNode, position = 'right top', options={}){
        return await this.msg(title, content, container, positionNode, position, options, 'success');
    }
}

export {Dialog, dialog, confirm};
