export default ".content{\r\n    background-color: var(--option-bg);\r\n    transition: background-color 0.2s, color 0.2s;\r\n    cursor: pointer;\r\n    overflow: hidden;\r\n}\r\n.content:hover{\r\n    background-color: var(--option-over-bg);\r\n    color: var(--option-over);\r\n}\r\n.content.selected{\r\n    font-weight: bold;\r\n    color: var(--option-selected);\r\n    background-color: var(--option-selected-bg);\r\n}\r\n.content.disabled{\r\n    opacity: 0.4;\r\n    cursor: not-allowed;\r\n}\r\n.content.disabled:hover{\r\n    background-color: var(--option-bg);\r\n}\r\n\r\n.area{\r\n    padding: 0 1em;\r\n    float: left;\r\n    line-height: 2em;\r\n    overflow: hidden;\r\n}\r\n\r\n.label, .text{\r\n    line-height: 2em;\r\n    overflow: hidden;\r\n    word-break: keep-all;\r\n    text-overflow: ellipsis;\r\n}\r\n"