export default "* {\r\n    box-sizing: border-box;\r\n}\r\n\r\n.group {\r\n    display: table;\r\n    align-items: center;\r\n    min-height: 1.5em;\r\n}\r\n\r\n.label {\r\n    white-space: nowrap;\r\n    transition: color 0.5s;\r\n    display: flex;\r\n    align-items: center;\r\n    color: var(--oo-color-text2);\r\n}\r\n\r\n.labelText {\r\n    margin-right: 0.5em;\r\n    padding: 0 0.35em;\r\n}\r\n\r\n.label-row{\r\n    display: table-cell;\r\n    vertical-align: middle;\r\n}\r\n\r\n.group.readmode .label-row{\r\n    display: table-cell;\r\n    vertical-align: middle;\r\n}\r\n\r\n.items {\r\n    display: block;\r\n}\r\n.group.readmode .items{\r\n    display: flex;\r\n}\r\n\r\n.hide-items{\r\n    position: absolute;\r\n    opacity: 0;\r\n    width: 1px;\r\n    height: 1px;\r\n    overflow: hidden;\r\n}\r\n.hide {\r\n    display: none;\r\n}\r\ndiv.invalidHint{\r\n    /*position: absolute;*/\r\n    color: var(--oo-color-error);\r\n    height: 0;\r\n    width: 0;\r\n    overflow: hidden;\r\n    word-break: break-all;\r\n    transition: margin-top 0.2s, height 0.2s;\r\n}\r\ndiv.invalidHint.show{\r\n    margin: 0.3em 0 0 0;\r\n    height: unset;\r\n    width: unset;\r\n    line-height: 1.2em;\r\n}\r\ndiv.invalidHint.show::before{\r\n    line-height: 1.4em;\r\n    margin-right: 0.2em;\r\n}\r\n.itemsContent{\r\n    border-radius: var(--oo-default-radius);\r\n    position: relative;\r\n}\r\n.group:has(div.invalidHint.show) .itemsContent{\r\n    background: var(--oo-color-error-bg);\r\n    box-shadow: var(--oo-shadow-border-error);\r\n}\r\n\r\ninput{\r\n    position: absolute;\r\n    width: 1px;\r\n    height: 1px;\r\n    overflow: hidden;\r\n    left: 50%;\r\n    bottom: 1px;\r\n    appearance: none;\r\n    outline: none;\r\n    border: 0;\r\n    opacity: 0;\r\n    font-size: 1px;\r\n}\r\n\r\n.requiredFlag{\r\n    color: var(--oo-color-highlight);\r\n    padding-left: 0.3em;\r\n    font-size: 0.75em;\r\n    display: table-cell;\r\n    vertical-align: middle;\r\n}\r\n"